import { mapGetters } from 'vuex'
export default {
  async asyncData({ $strapi, store, error }) {
    try {
      const response = await $strapi.$productos.find({ featured: true })
      store.commit('setFeaturedProducts', response)
    } catch (e) {
      error(e)
    }
  },
  data() {
    return {
      featuredProds: [],
    }
  },
  computed: {
    ...mapGetters(['featuredProducts']),
  },
}
